.wrap {
  padding-top: 22px;
  padding-bottom: 24px;
}

.header {
  margin-bottom: 14px;
}

.kicker {
  font-size: 12px;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: var(--muted);
  margin-bottom: 8px;
}

.h1 {
  font-size: 34px;
  line-height: 1.1;
  margin: 0;
}

.actions {
  margin-top: 12px;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.btnPrimary,
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;

  height: 42px;
  padding: 0 14px;

  border-radius: 999px;
  border: 1px solid var(--border);

  font-size: 14px;
  font-weight: 600;
  text-decoration: none;

  transition: transform 0.05s ease, background 0.15s ease;
}

.btnPrimary {
  background: black;
  color: white;
  border-color: black;
}

.btn {
  background: white;
  color: black;
}

.btnPrimary:hover,
.btn:hover {
  transform: translateY(-1px);
}

.viewer {
  width: 100%;
  border: 1px solid var(--border);
  border-radius: 18px;
  overflow: hidden;
  background: #fff;

  /* make it feel like a doc viewer */
  height: min(82vh, 980px);
}

.pdf {
  width: 100%;
  height: 100%;
  border: 0;
  display: block;
}
